import React, { FC, ReactNode } from 'react';
import { PseudoBox, Text } from '@chakra-ui/core';

interface TitleProps {
  children: ReactNode;
  style?: object;
}

export const Title: FC<TitleProps> = ({ children, style }) => (
  <PseudoBox
    width={'fit-content'}
    borderBottom={'2px'}
    borderColor={'transparent'}
    _hover={{
      transition: 'border-color 0.5s ease-in-out',
      borderColor: 'emerald-green.500',
    }}
  >
    <Text fontSize={'3xl'} style={style}>
      {children}
    </Text>
  </PseudoBox>
);
